.card {
  background: #ECF0F1;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 2.5px 5px 0px, rgba(0, 0, 0, 0.23) -2px 1px 6px 0px;
  width: 100%;
  min-height: 240px; }

.header {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center; }

.teamHeading {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  font-weight: bold; }

.body {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center; }

.dateInfo {
  text-align: center; }

.stage {
  font-style: italic;
  font-weight: bold; }



